.menu {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90px;

    @include tablets_1024 {
        height: auto;
        min-height: 80px;
        padding: 20px 0;
    }
}

.menu__logo {
    margin-right: 50px;
}

.menu__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-grow: 1;
    height: 100%;

    @include tablets_1024 {
        flex-direction: column;
        align-items: flex-start;
    }

    //@include phones__768 {
    //    position: absolute;
    //    top: 92px;
    //    left: 0;
    //    background: linear-gradient(90deg, #F1EDE6 0%, #FAF8F3 100%);
    //    width: 100%;
    //    height: auto;
    //}

    &--desktop {
        @include tablets_1024{
            display: none;
        }
    }
}

.menu__box {
    display: none;

    @include tablets_1024 {
        display: flex;
        justify-content: space-between;
        flex-grow: 1;
    }

    @include phones__768 {
        display: none;
        position: absolute;
        top: 98px;
        left: 0;
        z-index: 10;
        width: 100%;
        background-color: #ffffff;
    }
}

.menu__user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 60px;

    @include desktop_1366 {
        margin-left: 30px;
    }

    @include tablets_1024 {
        margin-left: 10px;
    }
}

.menu__item {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 5px;
    transition: 0.5s;

    &.active {

        .menu__link {
            color: $c-text-main;
        }
    }

    @include tablets_1024 {
        display: block;
        height: auto;
    }

    &:first-child {

        @include phones__768 {
            padding-top: 10px;
        }
    }

    &:last-child {

        @include phones__768 {
            padding-bottom: 10px;
        }
    }
}

.menu__item:not(:last-child) {
    @include tablets_1024 {
        margin-bottom: 20px;
    }
}

.menu__item ul[class] {
    display: none;
    position: absolute;
    top: 72px;
    left: -10px;
    min-width: 361px;
    padding: 12px;
    background-color: #F9F0E5;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
    border-radius: 2px;

    @include tablets_1024 {
        position: relative;
        top: auto;
        left: auto;
    }
}

.menu__subitem {
    padding-bottom: 8px;

    & a {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
    }
}

.menu__subitem:not(:last-child) {
    margin-bottom: 8px;
    border-bottom: 1px solid #F7F4ED;
}

.menu__link {
    display: flex;
    align-items: center;
    font-family: $font-second;
    font-weight: bold;
    font-size: 14px;
    line-height: 21px;
    color: $c-link-main;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.5s;

    &:hover {
        color: $c-link-hover;

        @include tablets_1024 {
            color: $c-text-main;
        }
    }

    @include desktop_1366 {
        font-size: 12px;
        line-height: 14px;
    }

    @include tablets_1024 {
        justify-content: center;
    }
    &.active {
        color: $c-link-main;
    }
}

.menu__articles {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 282px;
    height: 50px;
    font-family: $font-second;
    margin-right: 24px;
    font-weight: bold;
    font-size: 18px;
    line-height: 23px;
    color: #ffffff;
    background-color: $c-button-main;
    border-radius: 4px;
    cursor: pointer;
    transition: 0.5s;

    @include desktop_1366 {
        width: 170px;
        margin-right: 20px;
    }

    @media screen and (max-width: 940px) {
        width: 150px;
    }

    @media screen and (max-width: 850px) {
        width: 110px;
    }

    &:hover {
        opacity: 0.5;
    }

    .icon-user {
        display: block;
    }
}

.menu__languages {
    display: flex;
    flex-direction: column;
    border: 1px solid $c-button-main
}

.menu__language {
    padding: 5px;
    font-family: $font-main;
    font-size: 14px;
    line-height: 18px;
    color: $c-text-main;
    text-decoration: none;
    transition: 0.5s;

    &.active {
        color: #ffffff;
        background-color: $c-button-main;
    }

    &:hover {
        color: #ffffff;
        background-color: $c-button-main;
    }
}

.menu__arrow {
    display: block;
    width: 8px;
    height: 8px;
    margin-left: 8px;
    border-right: 1px solid $c-border;
    border-bottom: 1px solid $c-border;
    transform: rotate(45deg);
}

/*Изменения для активации поиска*/
.menu__list--search-active {
    opacity: 0;
    /*width: 100%;*/
}

.menu__login--search-active {
    display: none;
}

.menu__user--search-active {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    margin-left: 0;
}

.menu__languages--search-active {
    opacity: 0;
    visibility: hidden;
    display: none;
    /*width: 100%;*/
}

.menu__search {
    display: flex;
    justify-content: flex-end;
    margin-right: 20px;
    /*width: 100%;*/
}

.menu__search-form {
    display: flex;
    align-items: center;
    width: 33px;
    transition: 0.5s;

    @include tablets_1024 {
        width: 20px;
        height: auto;
    }
}

.menu__search-form--search-active {
    width: 100%;
    transition: 0.5s;
}

.menu__search-icon {
    flex-shrink: 0;
    width: 20px;
    height: auto;
    cursor: pointer;
}

.menu__search-form--search-active .menu__search-icon {
    margin-right: 24px;

    @include tablets_1024 {
        margin-right: 12px;
    }
}

.menu__search-input {
    display: none;
    font-size: 18px;
    line-height: 24px;
    color: #525252;
    background-color: transparent;
    outline: none;
    border: none;

    @include tablets_1024 {
        min-width: 100px;
    }
}

.menu__search-form--search-active .menu__search-input {
    display: block;
    flex-grow: 1;
    margin-right: 24px;

    @include tablets_1024 {
        margin-right: 12px;
    }
}

.menu__search-close {
    display: none;
    flex-shrink: 0;
    width: 18px;
    height: auto;
    cursor: pointer;
}

.menu__search-form--search-active .menu__search-close {
    display: block;
}

.menu__user--search-active .menu__articles {
    display: none;
}

.menu__user--search-active .menu__search {
    width: 100%;
}

.menu__left--search-active {
    opacity: 0;
    visibility: hidden;
}
